import React from 'react';
import Drink from '../drink/Drink_'
import { connect } from 'react-redux';
import { setRandomDrink } from '../drinks-actions'
import './drink-random.scss'

const stateToProps = state => {
    return {
        drink: state.drinks.drink
    };
};

function dispatchToProps(dispatch) {
    return { setRandomDrink: () => dispatch(setRandomDrink()) };
}

function DrinksRandom({ drink, setRandomDrink }) {
    return (<>
        <Drink drink={drink} />

        <div className='drinks-random'>
            <button onClick={() => setRandomDrink()}
                className='drinks-random'>

                <img src='categories/cat-random.svg' alt={''} class='drinks-random'></img>

                Losuj jeszcze raz

                <img src='categories/cat-random.svg' alt={''} class='drinks-random'></img>
            </button>

        </div>
    </>)
}

export default connect(stateToProps, dispatchToProps)(DrinksRandom);
