import React from 'react';
import { times } from 'lodash';
import { connect, batch } from 'react-redux';
import { ingCustomAdd, ingCustomRem, ingCustomRemIdx } from '../ings-actions'
import './ing-custom.scss'

const stateToProps = state => {
    const { ingsCounts, left, ingsOrder } = { ...state.drinks.drinkCustom }

    const orders = ingsOrder.reduce((acc, ingId, idx) => {
        const ingPos = (acc[ingId] || [])
        ingPos.push(idx + 1)
        acc[ingId] = ingPos
        return acc;
    }, {})

    return {
        ingsCounts,
        left,
        orders
    };
};

function dispatchToProps(dispatch) {
    return {
        addIng: (ing, n) => {
            batch(() => times(n, () => dispatch(ingCustomAdd(ing))))
        },
        remIng: ing => dispatch(ingCustomRem(ing)),
        remIngByIdx: idx => dispatch(ingCustomRemIdx(idx))
    };
}

function IngsCustom({ ing, left, addIng, ingsCounts, orders }) {

    const ingCount = ingsCounts[ing.id] || 0
    const canAdd = ing.portion <= left

    return (<div
        onClick={() => canAdd ? addIng(ing, 1) : false}
        className='ing-custom'>

        <p className='ing-custom'>{ing.name + ' ' + ingCount}</p>

        {[0, 1, 2, 3].map(idx => {

            const isSelected = idx < ingCount
            const nrOfPortions = idx + 1 - ingCount
            const isVisible = !isSelected && nrOfPortions * ing.portion <= left
            const isHiddenCss = isVisible ? '' : ' ing-custom-hidden'

            const cssClass = isSelected
                ? ' ing-custom-selected'
                : isHiddenCss

            return (<span key={ing.id + idx}
                className={'ing-custom' + cssClass}>
                &nbsp;
            </span>)
        }
        )}
    </div>)
}

export default connect(stateToProps, dispatchToProps)(IngsCustom);
