import React from 'react';
import { connect } from 'react-redux'
import { DRINKS_LIST_VIEW_FIND_BY_NR, DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import { setDrinksListView } from '../drinks-actions'
import { findDrinkByNr } from '../../drinks/drinks-actions'

const stateToProps = state => {
    const { view } = state.drinks.drinksList
    return {
        view
    };
};

function dispatchToProps(dispatch) {

    return {
        setDrinksListView: (view => {
            if (DRINKS_LIST_VIEW_LIST === view) {
                dispatch(findDrinkByNr(0))
            }

            dispatch(setDrinksListView(view))
        }),
    };
}

function DrinksFindByIdxBtn({ view, setDrinksListView }) {

    const nrBtn = <button
        onClick={() => setDrinksListView(DRINKS_LIST_VIEW_FIND_BY_NR)}
        className='drinks-find-by-nr'>Podaj nr drinka</button>

    const listBtn = <button
        onClick={() => setDrinksListView(DRINKS_LIST_VIEW_LIST)}
        className='drinks-find-by-nr'>WrÃ³Ä do peÅnej listy</button>

    return (DRINKS_LIST_VIEW_LIST === view
        ? nrBtn
        : (DRINKS_LIST_VIEW_FIND_BY_NR === view
            ? listBtn
            : '')
    )
}

export default connect(stateToProps, dispatchToProps)(DrinksFindByIdxBtn)
