import React from 'react';
import DrinkList from '../drinks-list/DrinksList'
import Ings from '../../ings/Ings'
import { connect } from 'react-redux'
import DrinkPrefs from './DrinkPrefs_';
import { DRINKS_PREFS_VIEW_INGS, DRINKS_PREFS_VIEW_DRINKS_LIST } from '../drinks-reducer'
import './drinks-prefs.scss'

const stateToProps = state => {
    const { view } = state.drinks.drinksPrefs

    return {
        view
    };
};

function DrinksPrefs({ view }) {

    if (DRINKS_PREFS_VIEW_INGS === view) {
        return (<>
            <DrinkPrefs />
            <Ings />
        </>)
    }

    if (DRINKS_PREFS_VIEW_DRINKS_LIST === view) {
        return <DrinkList />
    }

    return 'NO DRINKS PREFS VIEW'
}

export default connect(stateToProps)(DrinksPrefs);
