import React from 'react';
import { connect } from 'react-redux'
import DrinksCount from '../../../drinks/partials/DrinksCount_'
import DrinksFindByNrBtn from '../../../drinks/partials/DrinksFindByNrBtn'
import DrinksFilterBtn from '../../../drinks/partials/DrinksFilterBtn'
import './category-aside.scss'
import './category-aside-list.scss'

const stateToProps = state => {
    const { drinks } = state.drinks
    return {
        drinks,
    };
};

function CategoryAsideList({ drinks }) {

    return (<div className='cat-aside cat-aside-list'>

        <DrinksCount drinks={drinks} />

        <DrinksFindByNrBtn />

        <DrinksFilterBtn />

    </div>)
}

export default connect(stateToProps)(CategoryAsideList)
