import React from 'react';
import { connect } from 'react-redux'
import { DRINKS_LIST_VIEW_FILTER, DRINKS_LIST_VIEW_LIST } from '../drinks-reducer'
import { setDrinksListView } from '../drinks-actions'
import { filterDrinks } from '../../drinks/drinks-actions'

const stateToProps = state => {
    const { view } = state.drinks.drinksList
    return {
        view
    };
};

function dispatchToProps(dispatch) {

    return {
        setDrinksListView: (view => {
            if (DRINKS_LIST_VIEW_LIST === view) {
                dispatch(filterDrinks(''))
            }

            dispatch(setDrinksListView(view))
        })
    };
}

function DrinksFilterBtn({ view, setDrinksListView }) {

    const filterBtn = <button
        onClick={() => setDrinksListView(DRINKS_LIST_VIEW_FILTER)}
        className='drinks-filter'>Wyszukaj po nazwie lub skÅadniku</button>

    const listBtn = <button
        onClick={() => setDrinksListView(DRINKS_LIST_VIEW_LIST)}
        className='drinks-filter'>WrÃ³Ä do peÅnej listy</button>

    return (DRINKS_LIST_VIEW_LIST === view
        ? filterBtn
        : (DRINKS_LIST_VIEW_FILTER === view
            ? listBtn
            : '')
    )
}

export default connect(stateToProps, dispatchToProps)(DrinksFilterBtn)
